<template>
  <main-menu v-if="false" />
  <animations v-if="true" />
  <fractions v-if="false" />
</template>

<script>
import { defineComponent } from "vue";
import mainMenu from "./interfaces/main-menu/index.vue";
import animations from "./interfaces/animations/index.vue";
import fractions from "./interfaces/fractions/index.vue";

export default defineComponent({
  name: "App",
  components: {
    mainMenu,
    animations,
    fractions,
  },
  data() {
    return {
      zoomEl: undefined,
    };
  },
  mounted() {
    window.addEventListener("resize", this.setZoom);
    this.zoomEl = document.createElement("style");
    document.querySelector("head").appendChild(this.zoomEl);
    this.setZoom();
  },
  unmounted() {
    window.removeEventListener("resize", this.setZoom);
  },
  methods: {
    setZoom() {
      let zoom = parseFloat(
        Math.min(
          3,
          +(
            Math.sqrt(window.innerWidth ** 2 + window.innerHeight ** 2) /
            2202.9071700822983
          ).toFixed(3)
        )
      );
      //  zoom > 2 ? zoom = 1.7 : ""; //тут можно доп условия прописать чтобы была больше на мониках которые меньше и тд...
      // ну и ща хуевато работает, тк нужно верстку заново под нее переписать

      this.zoomEl.innerHTML = `* { --zoom: ${zoom}; }`;
      console.log(zoom);
      return { "--zoom": zoom };
    },
  },
});
</script>

<style lang="scss">
#app {
  --zoom: 1;
}

